class UtilService {

    isValidDate = (d) => {
        return d instanceof Date && !isNaN(d);
    }

    addLeadingZeros(n) {
        if (n <= 9) {
          return "0" + n;
        }
        return n
    }

    getDate(date) {
        return this.isValidDate(new Date(date)) ? date.substring(0,10) : '';
    }

}

export default new UtilService()