import React, { Component } from 'react';

export default class Project extends Component {

    state = {
        showDescription: false
    };

    handleDescription = () => {
        this.setState({
            showDescription: !this.state.showDescription
        })
    }

    render() {
        let { project } = this.props;
        let { title, description, picture, icons, source } = project;
        return (
            <div className="project">
                <div className="icons">
                    {icons.map(icon =>
                        <i className={icon} key={icon}></i>)
                    }
                </div>
                <h3>{title}</h3>
                <img src={picture} alt="" onClick={this.handleDescription} />
                <span className="infos" onClick={this.handleDescription}>
                    <i className="fas fa-plus-circle"></i>
                </span>

                {
                    this.state.showDescription && (
                        <div className="showInfos">
                            <div className="infosContent">
                                <div className="head">
                                    <h2>{title}</h2>
                                    <div className="sourceCode">
                                        <a href={source} rel="noopener noreferrer" className="button" target="_blank">Source code</a>
                                    </div>
                                </div>

                                <p className="text">{description}</p>

                                <div className="button return" onClick={this.handleDescription}>Back</div>
                            </div>
                        </div>
                    )
                }
            </div>
        );
    }
}

