import React, { Component } from 'react';

class Experience extends Component {

    constructor(props) {
        super(props);

        this.state = { projects: [] }
    }    

    componentDidMount() {
        this.callAPI();
    }

    callAPI(){
       fetch('http://localhost:5500/projects')
         .then(res => res.json())
         .then(res => {this.setState({ projects: res })
         })
   
    }

    validDate(myDate) {
        if (myDate) return myDate.substring(0,10);
        return '';
    }

    render () {
        return (
            <div className="experience">
                <h3>Projects</h3>
                {this.state.projects.map(project => {
                    let startDate =this.validDate (project.startDate);
                    let endDate =this.validDate (project.endDate);
                    if (!endDate) endDate = "Present";
                     return (
                        <div key={`project-${project._id}`} className="languageList">
                            <h4>{project.title}</h4>
                            <h5>{startDate} - {endDate}</h5>
                            <p>{project.description}</p>
                        </div>
                     )
                })}

            </div>
        )
    }
}

export default Experience;