import React, { Component } from 'react'
import { withRouter } from 'react-router';
import ProjectService from '../../services/ProjectService';
import UtilService from '../../services/UtilService';

class ReadProject extends Component {
    constructor(props) {
        super(props)

        this.state = {
                projects: []
        }
        this.addProject = this.addProject.bind(this);
        this.editProject = this.editProject.bind(this);
        this.deleteProject = this.deleteProject.bind(this);
    }

    deleteProject(id){
        ProjectService.deleteProject(id).then( res => {
            this.setState({projects: this.state.projects.filter(project => project._id !== id)});
        });
    }

    editProject(id){
        this.props.history.push(`/add-project/${id}`);
    }

    componentDidMount(){
        ProjectService.getProjects().then((res) => {
            this.setState({ projects: res.data});
        });
    }

    addProject(){        
        this.props.history.push('/add-project/_add');
    }

    render() {
        return (
            <div>
                 <h2 className="text-center">Projects List</h2>
                 <div className = "header">
                    <button className="btn btn-primary" onClick={this.addProject}> Add Project</button>
                 </div>
                 <br></br>
                 <div className = "form">
                        <table className = "table table-striped table-bordered">

                            <thead>
                                <tr key = {0}>
                                    <th> Title</th>
                                    <th> Source</th>
                                    <th> Start date</th>
                                    <th> End date</th>
                                    <th> Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.projects.map(
                                        project => {
                                            return (
                                 
                                                <tr key = {project._id}>
                                                    <td> { project.title} </td>   
                                                    <td> {project.source}</td>
                                                    <td> {UtilService.getDate(project.startDate)}</td>
                                                    <td> {UtilService.getDate(project.endDate)}</td>
                                                    <td>
                                                        <button onClick={ () => this.editProject(project._id)} className="btn btn-info">Update </button>
                                                        <button style={{marginLeft: "10px"}} onClick={ () => this.deleteProject(project._id)} className="btn btn-danger">Delete </button>
                                                    </td>
                                                </tr>
                                            )}
                                    )
                                }
                            </tbody>
                        </table>

                 </div>

            </div>
        )
    }
}

export default withRouter(ReadProject)