import React, { Component } from 'react'
import ProjectService from '../../services/ProjectService';
import { Multiselect } from "multiselect-react-dropdown";
import { withRouter } from 'react-router';
import UtilService from '../../services/UtilService';


class CreateProject extends Component {
    constructor(props) {
        super(props)

        this.state = {
            id: this.props.match.params.id,
            title: '',
            startDate: '',
            endDate: '',
            picture: '',
            description: '',
            source: '',
            languages: [],
            icons: []
        }
        this.changeTitleHandler = this.changeTitleHandler.bind(this);
        this.changeStartDateHandler = this.changeStartDateHandler.bind(this);
        this.changeEndDateHandler = this.changeEndDateHandler.bind(this);
        this.changePictureHandler = this.changePictureHandler.bind(this);
        this.changeDescriptionHandler = this.changeDescriptionHandler.bind(this);
        this.changeSourceHandler = this.changeSourceHandler.bind(this);
        this.changeLanguagesHandler = this.changeLanguagesHandler.bind(this);
        this.saveOrUpdateProject = this.saveOrUpdateProject.bind(this);
    }

    getLanguageOptions() {
        const languageOptions = [
            {id: 1, name:'javascript', icon: "fab fa-js"}, 
            {id: 2, name: 'react', icon: "fab fa-react"}, 
            {id: 3, name:'css', icon: "fab fa-css3-alt"}, 
            {id: 4, name: 'java', icon: "fab fa-java"}, 
            {id: 5, name: 'jira', icon: "fab fa-jira"}
];

        return languageOptions;
    }

    componentDidMount(){
        if(this.state.id === '_add'){
            return
        }else{
            ProjectService.getProjectById(this.state.id).then( (res) =>{
                let project = res.data;
                this.setState({title: project.title,
                    startDate: project.startDate,
                    endDate: project.endDate,
                    picture: project.picture,
                    description: project.description,
                    source: project.source,
                    languages: project.languages,
                    icons: project.icons
                });
            });
        }        
    }

    saveOrUpdateProject = (e) => {
        e.preventDefault();
        let project = {title: this.state.title, startDate: this.state.startDate, endDate: this.state.endDate, picture: this.state.picture, description: this.state.description, source: this.state.source, languages: this.state.languages, icons: this.state.icons};
        console.log('project => ' + JSON.stringify(project));

        if(this.state.id === '_add'){
            ProjectService.createProject(project).then(res =>{
                this.props.history.push('/admin');
        });
        }else{
            ProjectService.updateProject(project, this.state.id).then( res => {
                this.props.history.push('/admin');
            });
        }
    }
    
    changeTitleHandler= (event) => {
        this.setState({title: event.target.value});
    }

    changeStartDateHandler= (event) => {
        this.setState({startDate: event.target.value});
    }

    changeEndDateHandler= (event) => {
        this.setState({endDate: event.target.value});
    }

    changePictureHandler= (event) => {
        this.setState({picture: event.target.value});
    }

    changeDescriptionHandler= (event) => {
        this.setState({description: event.target.value});
    }

    changeSourceHandler= (event) => {
        this.setState({source: event.target.value});
    }

    changeLanguagesHandler = (event) => {
        this.setState({languages: Array.isArray(event) ? event.map(x => x.name) : []});
        const filteredLanguages = event.filter(language => {
            return this.getLanguageOptions().find((option)=>option.name === language.name);
        });
        this.setState({icons: Array.isArray(filteredLanguages) ? filteredLanguages.map(x => x.icon) : []})
    }


    cancel(){
        this.props.history.push('/admin');
    }

    getLegend(){
        if(this.state.id === '_add'){
            return <h3 className="text-center">Add Project</h3>
        }else{
            return <h3 className="text-center">Update Project</h3>
        }
    }

    render() {
        return (
            <div>
                <br></br>
                   <div className = "container">
                        <div className = "row">
                            <div className = "card col-md-10 offset-md-1 offset-md-1">
                                {
                                    this.getLegend()
                                }
                                <div className = "card-body">
                                    <form>
                                        <div className = "form-group">
                                            <label> Title: </label>
                                            <input placeholder="Title" name="title" className="form-control" 
                                                value={this.state.title} onChange={this.changeTitleHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Description: </label>
                                            <textarea placeholder="Description" name="description" className="form-control" 
                                                value={this.state.description} onChange={this.changeDescriptionHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Languages: </label>
                                            <Multiselect 
                                                options={this.getLanguageOptions()} 
                                                isObject={true}
                                                displayValue="name"
                                                selectedValues={this.getLanguageOptions().filter(object => {
                                                    return this.state.languages.find((language)=>language === object.name);
                                                })} 
                                                value={this.state.languages}
                                                closeIcon="circle"
                                                onSelect={this.changeLanguagesHandler}
                                                onRemove={this.changeLanguagesHandler}
                                            />
                                        </div>
                                        <div className = "form-group">
                                            <label> Start date: </label>
                                            <input type="date" placeholder="start date" name="startDate" className="form-control" 
                                                value={UtilService.getDate(this.state.startDate)} onChange={this.changeStartDateHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> End date: </label>
                                            <input type="date" placeholder="End date" name="endDate" className="form-control" 
                                                value={UtilService.getDate(this.state.endDate)} onChange={this.changeEndDateHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Picture: </label>
                                            <input placeholder="Picture" name="picture" className="form-control" 
                                                value={this.state.picture} onChange={this.changePictureHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Source: </label>
                                            <input placeholder="Source" name="source" className="form-control" 
                                                value={this.state.source} onChange={this.changeSourceHandler}/>
                                        </div>

                                        <button className="btn btn-success" onClick={this.saveOrUpdateProject}>Save</button>
                                        <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px"}}>Cancel</button>
                                    </form>
                                </div>
                            </div>
                        </div>

                   </div>
            </div>
        )
    }
}

export default withRouter(CreateProject)